}
.viewapply-content-normal {
  .viewapply-content-item {
    width: 47%;
    display: inline-flex;
    padding: 15px 20px;
  }
  .viewapply-content-item-title {
    font-size: 14px;
    color: #6F7071;
    width: 200px;
    display: flex;
    flex-shrink: 0;
  }
  .viewapply-content-item-content {
    font-size: 14px;
    color: #35383A;
    display: flex;
    flex-grow: 1;
    min-width: 0px;
  }
}

</style>



