<template>
  <div class="introduce-bottom">
    <h2>现在开始使用XPE进行投资管理</h2>
    <h3>让您的私募股权投资管理轻松、高效、安全、智能</h3>
    <div class="input-box">
      <input type="text" placeholder="你的手机号码" v-model="phone">
      <button @click="toRegiste">免费注册</button>
    </div>
    <div class="ewm-items-box">
      <div class="ewm-item-box">
        <img :src="android" alt="">
        <div>
          <img  src="../../images/introducePage_icon_26.png" alt="">
          <span>XPE 安卓版</span>
        </div>
        <span>版本{{android_version}} &nbsp;{{android_time}}</span>
      </div>
      <div class="ewm-item-box">
        <img :src="ios" alt="">
        <div>
          <img src="../../images/introducePage_icon_27.png" alt="">
          <span>XPE iOS版</span>
        </div>
        <span>版本{{ios_version}} &nbsp;{{ios_time}}</span>
      </div>
    </div>
    <div class="ewm-box">
      <!-- <span style="cursor: pointer;">免责声明</span> -->
      <a href="http://prehts.pewinner.com/uploads/xieyi/免责声明.pdf" target="_blank"> 免责声明</a>
      <!-- <span>客户端下载</span>
      <div class="ewm-imgbox">
        <img src="../../images/introducePage_icon_27.png" alt="" class="ewm-img1">
        <div class="ewm_item1">
          <img  :src="ios" alt="">
        </div>
      </div>
      <div class="ewm-imgbox">
        <img src="../../images/introducePage_icon_26.png" alt="" class="ewm-img1">
        <div class="ewm_item1">
          <img :src="android" alt="">
        </div>
      </div> -->
    </div>
  </div>
</template>
<script>
import {
  latestApp
} from '../../config/api'
export default {
  data() {
    return {
      phone: '',
      android:'',
      android_time:'',
      android_version:'',
      ios:'',
      ios_time:'',
      ios_version:'',
    }
  },
  components: {},
  created() {
    this.initData();
  },
  mounted() {},
  computed: {

  },
  methods: {
    toRegiste() {
      this.$router.push({
        path: '/register',
        query: {
          phone: this.phone
        }
      })
    },
    initData(){
      latestApp().then(res=>{
        if(res.data.msgNo==1){
          this.android = res.data.payload.android.path;
          this.android_time = res.data.payload.android.add_time.split(' ')[0];
          this.android_version = res.data.payload.android.version;
          this.ios = res.data.payload.ios.path;
          this.ios_time = res.data.payload.ios.add_time.split(' ')[0];
          this.ios_version = res.data.payload.ios.version;
        }
      })
    },
  }
}

</script>
<style lang="scss" scoped>
.introduce-bottom {
  padding: 99px 0 16px 0;
  background-color: #F9FCFF;
  text-align: center;
  h2 {
    font-size: 40px;
    color: #35383A;
  }
  h3 {
    font-size: 18px;
    color: #35383A;
    margin: 26px 0 36px 0px;
  }
  .input-box {
    input {
      width: 500px;
      height: 50px;
      background-color: #ffffff;
      border: 1px solid #ECECEC;
      border-radius: 25px;
      text-indent: 30px;
      font-size: 16px;
      margin-right: 26px;
    }
    button {
      width: 145px;
      height: 50px;
      background:linear-gradient(130.4deg,rgba(3,169,244,1),rgba(15,201,255,1));
      color: white;
      border-radius: 25px;
    }
  }
  .ewm-box {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    &>span {
      font-size: 14px;
      color: #71777D;
      padding: 0px 38px;
    }
    .ewm-imgbox{
      position: relative;
      margin-right: 40px;
    }
    .ewm-img1 {
      width: 28px;
      height: 28px;
      cursor: pointer;
      &:hover+.ewm_item1 {
        visibility:visible;
        transform:translate(-50%,0px);
        opacity: 1;
        transition:all 0.3s;
      }
    }
    .ewm-img1:hover {
      opacity: 0.6;
    }
    a {
      font-size: 12px;
      color: #71777D;
      padding: 0px 38px;
      cursor: pointer;
    }
    .ewm_item1 {
      width: 200px;
      height: 235px;
      background: url('../../images/introducePage_icon_29.png') no-repeat center;
      background-size: 100%;
      position: absolute;
      visibility:hidden;
      left: 50%;
      top: -230px;
      opacity: 0;
      transform:translate(-50%,-10px);
      transition:all 0.3s;
      img {
        width: 180px;
        height: 180px;
        margin-top: 21px;
      }
    }
  }
}
.ewm-items-box{
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-top: 30px;
  .ewm-item-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 30px;
    font-size: 12px;
    &>img{
      width: 126px;
      height: 126px;
    }
    &>span{
      color: #A0A4AA;
      letter-spacing:1px
    }
    &>div{
      display: flex;
      flex-direction: row;
      align-items: center;
      margin: 3px 0px;
      img{
        width: 12px;
        height: 12px;
        margin-right: 5px;
      }
      span{
        color: #35383A;
        font-weight: bold;
      }
    }
  }
}
</style>



