<template>
	<div class="progress-bar-container">
		<div class="progress-bar-line-bottom">
			<div class="progress-bar-line-top" :style="{width:schedule}">
			</div>
			<div class="progress-bat-tip" :style="{left:schedule}">
				{{schedule}}
				<span class="triangle"></span>
			</div>
		</div>		
	</div>
</template>
<script>
    export default {
    	data(){
            return{
               
            }
        },
        mounted(){

        },
        created(){
        },
        computed:{
        	spancenter:function(){
        		if(this.schedule>='50%'||this.schedule=='100%'){
        			return true;
        		}else{
        			return false;
        		}
        	},
        	spanright:function(){
        		if(this.schedule=='100%'){
        			return true;
        		}else{
        			return false;
        		}
        	}
        },
        props: ["schedule"],
        methods: {
        }
    }
</script>
<style lang="scss" scoped>
  @import "./../style/mixin.scss";
  .progress-bar-container{
  	width: 100%;
  	height: 8px;
  	background: #fff;
  	border-radius: 4px;
  	// box-shadow: 0px 0px 10px 0px rgba(72,140,220,0.25);
  	position: relative;
  	cursor: pointer;
  }
  .progress-bar-line-bottom{
  	@include ct;
  	height: 8px;
  	background: #F2F3F4;
  	position: absolute;
  	left: 0px;
	right: 10px;
	border-radius: 4px;
  }
  .progress-bar-line-top{
  	position: absolute;
  	left: 0px;
  	height: 100%;
  	background: #5EA9FF;
	  transition:all 0.5s;
	  border-radius: 4px;
  }
  .progress-bar-spanbox span{
  	display: block;
  	width: 16px;
  	height: 16px;
  	background: #F2F3F4;
  	border-radius: 100%;
  }
  .progress-bar-spanbox span.current{
  	background: #B1D2F9;
  }
  .progress-bar-spanbox span.span-left{
  	background: #B1D2F9;
  }
  .progress-bar-spanbox span.span-center{
  	@include center;
  }
  .progress-bar-spanbox span.span-right{
  	position: absolute;
  	right: 0px;
  	top: 0px;
  }
  .progress-bar-spanbox{
  	position: absolute;
  	left: -8px;
  	right: -8px;
  	height: 16px;
  	@include ct;
  }

  .progress-bat-tip{
  	position: absolute;
  	/* visibility: hidden;
  	opacity: 0; */
  	transition:all 0.5s;
  	top: -32px;
  	transform: translateX(-50%);
  	padding: 4px 8px;
  	line-height: 1em;
  	border-radius: 3px;
  	background: #5EA9FF;
  	font-size: 14px;
  	color: #fff;
  }
  /* .progress-bar-container:hover .progress-bat-tip{
  	visibility: visible;
  	opacity: 1;
  	top: -28px;
  } */
  .triangle{
  	display: block;
  	width: 0; 
	height: 0; 
	position: absolute;
	@include cl;
	top: 100%;
	border-left: 5px solid transparent; 
	border-right: 5px solid transparent; 
	border-top: 4px solid #5EA9FF; 
  }
</style>


