<template>
	<div class="">
		<div class="dialog_content flex_between">
			<div class="dialog_content_left">
				<div class="dialog_content_title">
					选择人员
				</div>
				<div class="dialog_input_box">
					<el-input placeholder="输入关键字进行过滤" v-model="filterText" clearable>
					</el-input>
				</div>
				<div class="dialog_content_main">
					
					<el-tree class="filter-tree" :filter-node-method="filterNode" @check-change="dialogCheckboxChange" :data="departmentData" show-checkbox node-key="id" ref="tree" highlight-current :props="defaultProps">
					</el-tree>
				</div>

			</div>
			<div class="dialog_content_right">
				<div class="dialog_content_title">
					已选人员
				</div>
				<div class="dialog_content_main">
					<div v-for="(item,index) in selectedEmployeeData" :key="index" class="dialog_content_main_list">
						{{ item.name }}
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { getCompanyDepartmentApi } from '../config/api'
	
	export default {
		name: 'setPower',
		data() {
			return {
				newIds: [],//已有权限的用户id添加标识
				selectedEmployeeData: [],
				departmentData: [],
				defaultProps: {
					children: 'children',
					label: 'name'
				},
				
				filterText: '',
			}
		},
		created() {
			this.getInitData()
		},
		props: ['havePowerUserIds'],
		mounted() {},
		components: {},
		methods: {
			getInitData(){
				this.selectedEmployeeData = [];
				this.newIds = [];
				getCompanyDepartmentApi({
					type: 2
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.addHavePowerIdsUnionId(res.data.payload,this.havePowerUserIds);
						this.departmentData = this.addUnionId(res.data.payload);
						console.log(this.newIds)
						this.$refs.tree.setCheckedKeys(this.newIds);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			dialogCheckboxChange(node, isSelected) {
				var selectedNodes = this.$refs.tree.getCheckedNodes(true);
				this.getAllEmployeeData(selectedNodes)
			},
//			getSelectDepartment() {
//				var ids = this.$refs.tree.getCheckedKeys(true);
//				this.dialogParam.depart_id = ids.join();
//				var nodes = this.$refs.tree.getCheckedNodes(true);
//				var names = [];
//				for(var i in nodes) {
//					names.push(nodes[i].label);
//				}
//				this.departmentNames = names.join()
//				console.log(names);
//				this.innerVisible = false;
//			},
			getAllEmployeeData(data){
				this.selectedEmployeeData = [];
				var idsArray = [];
				for(var i in data){
					var id = data[i].id;
					var ids = id.split('-');
					data[i].id = ids[0];
					if (idsArray.indexOf(data[i].id) == -1) {
						idsArray.push(data[i].id);
						this.selectedEmployeeData.push(data[i]);
					}
				}
				console.log()
				this.$emit('get-set-power-id',this.selectedEmployeeData)
			},
			//给员工的id加一个标识
			addUnionId(data){
				for(var i in data){
					if (data[i].children) {
						this.$set(data[i],'disabled',true);
						this.addUnionId(data[i].children)
					} else{
						if (data[i].is_user) {
							data[i].id = data[i].id + '-' + data[i].one_department_id;
						} else {
							this.$set(data[i],'disabled',true);
						}
					}
				}
				return data
			},
			//给已有权限的员工ids添加标识
			addHavePowerIdsUnionId(data,ids){
				for(var i in data){
					if (data[i].children) {
						this.addHavePowerIdsUnionId(data[i].children,this.havePowerUserIds)
					} else {
						if (data[i].is_user) {
							if(ids.indexOf(data[i].id) >= 0){
								var item = data[i].id + '-' + data[i].one_department_id;
								this.newIds.push(item);
								var is_have_in_selected_data = this.isHaveInSelectedData(data[i]);
								if (!is_have_in_selected_data) {
									this.selectedEmployeeData.push(data[i])
								}
							}
						}
					}
				}
			},
			// 判断初始化的时候人员id是否重复
			isHaveInSelectedData(item){
				var data = this.selectedEmployeeData;
				for (var i in data) {
					if (data[i].id == item.id) {
						return true
					}
				}
				return false
			},
			
			filterNode(value, data) {
				if (!value) return true;
        			return data.name.indexOf(value) !== -1;
      		}
		},
		watch: {
			havePowerUserIds: function(e){
				this.filterText = '';
				this.getInitData()
			},
			filterText(val) {
		        this.$refs.tree.filter(val);
	      	}
		}
	}
</script>

<style scoped="scoped">
	/*设置权限弹出框*/
	
	.dialog_content {
		height: 340px;
		overflow: hidden;
	}
	
	.dialog_content>div {
		width: 50%;
		height: 340px;
		font-size: 14px;
		color: #545454;
	}
	
	.dialog_content_right {
		box-sizing: border-box;
		text-align: center;
		border-left: 1px solid #E5E5EA;
	}
	
	.dialog_content_title {
		margin-bottom: 40px;
		text-align: center;
	}
	
	.dialog_content_main {
		height: 200px;
		overflow-y: scroll;
	}
	
	.dialog_content_main_list {
		margin-bottom: 20px;
	}
	.dialog_content_main_list:last-child{
		margin-bottom: 30px;
	}
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	/*设置权限弹出框结束*/
	
	.dialog_input_box{
		width: 300px;
		margin-bottom: 20px;
	}
</style>


