              //item.amount.replace("万元", "");
              item.percent.replace("%", "");
              let pattern1 = "万元";
              let pattern2 = "%";
              //item.amount = item.amount.replace(new RegExp(pattern1), "");
              item.percent = item.percent.replace(new RegExp(pattern2), "");
              item.percent = parseFloat(item.percent);
              if (item.logo == null) {
                item.logo = "../../static/images/company_headerimg_normal.png ";
              }
            });
            // console.log(data);
            this.relax1 = data;
          } else {
            dialogConfirm("info", res.data.message);
          }
        });
      },
  
      //获取工商信息
      getGsInfo(id) {
        sgCompanyInfo({
          company_id: id
        }).then(res => {
          if (res.data.msgNo == 1) {
            let data = res.data.payload;
            if (data == null) {
              this.relax2 = {};
            } else {
              this.relax2 = data;
            }
          } else {
            dialogConfirm("info", res.data.message);
          }
        });
      },
  
      //获取变更记录
      getBgNotes(id) {
        ChangeInfo({
          company_id: id,
          page: this.currentPage,
          number: this.pageSize
        }).then(res => {
          // console.log(res);
          if (res.data.msgNo == 1) {
            // let data = res.data.payload;
            // this.relax3 = data;
            this.relax3 = res.data.payload.data;
            this.total = res.data.payload.total;
          } else {
            dialogConfirm("info", res.data.message);
          }
        });
      },
  
      //获取企业年报
      getQynbInfo(id) {
        Annualreport({
          company_id: id
        }).then(res => {
          if (res.data.msgNo == 1) {
            let data = res.data.payload;
            if (data.length > 0) {
              this.relax4 = data;
              this.relax4Item = this.relax4[0];
            }
          } else {
            dialogConfirm("info", res.data.message);
          }
        });
      },
  
      //获取股权结构
      getGqjgInfo(id) {
        equityRatio({
          company_id: id
        }).then(res => {
          if (res.data.msgNo == 1) {
            let data = res.data.payload;
            this.relax5 = data.structure;
            this.loadData(this.relax5);
          } else {
            dialogConfirm("info", res.data.message);
          }
        });
      },
  
      // 循环加载数据
      loadData(arr) {
        arr.forEach(item => {
          item.showName =
            "股东:" +
            item.name +
            "     /股权比例:" +
            item.percent +
            "     /出资:" +
            item.amount;
          if (item.children != undefined && item.children.length != 0) {
            this.loadData(item.children);
          } else {
            return false;
          }
        });
      },
      //获取对外投资信息
      getDwtzInfo(id) {
        Invest({
          company_id: id
        }).then(res => {
          if (res.data.msgNo == 1) {
            let data = res.data.payload;
            // console.log(data);
            this.relax6 = data;
            // console.log(this.relax6.length);
            // if(data.length>0){
            //   this.relax6 = data;
            // }
          } else {
            dialogConfirm("info", res.data.message);
          }
        });
      },
  
      //获取负面信息和企业舆情
      getFmxxInfo(id, type) {
        newsData({
          company_id: id,
          page: 1,
          pageSize: 100000,
          type: type
        }).then(res => {
          if (res.data.msgNo == 1) {
            let data = res.data.payload;
            if(!data){
              data = [];
            }
            data.forEach(element => {
              element.tag = element.tag.split("#");
            });
            if (type == 1) {
              this.relax8 = data; //f负面信息
              this.navList[8].count = data.length;
            } else if (type == 2) {
              this.relax7 = data; //企业舆情
              this.navList[7].count = data.length;
            }
          } else {
            dialogConfirm("info", res.data.message);
          }
        });
      },
  
      //切换企业年报导航
      cutQynbNav(index) {
        this.relax4Item = this.relax4[index];
        this.qynbId = index;
      },
      getCompanyInfo() {
        projectLsit({
          page: 1,
          number: 10,
          id: this.company_id
        }).then(res => {
          // console.log(res);
          if (res.data.msgNo == 1) {
            this.projInfo = res.data.payload.data[0];
          }
        });
      },
  
      //获取法律诉讼信息
      getFlssInfo() {
        lawSuit({
          company_id: this.company_id,
          page: this.currentPage,
          number: this.pageSize
        }).then(res => {
          // console.log(res);
          if (res.data.msgNo == 1) {
            this.relax9 = res.data.payload.data;
            this.total = res.data.payload.total;
            this.navList[6].count = res.data.payload.total;
          } else {
            dialogConfirm("info", res.data.message);
          }
        });
      },
  
      //切换导航
      cutNav(step) {
        this.step = step;
        this.currentPage = 1;
        if (step == 1) {
          this.getGsInfo(this.company_id); //工商信息
        } else if (step == 2) {
          this.getGdInfo(this.company_id); //股东信息
        } else if (step == 3) {
          this.getBgNotes(this.company_id); //变更记录
        } else if (step == 4) {
          this.getQynbInfo(this.company_id); //企业年报
        } else if (step == 5) {
          this.getGqjgInfo(this.company_id); //股权结构
        } else if (step == 6) {
          this.getDwtzInfo(this.company_id); //对外投资
        } else if (step == 7) {
          this.getFmxxInfo(this.company_id, 2); //企业舆情 
        } else if (step == 8) {
          this.getFmxxInfo(this.company_id, 1); //负面信息
        } else if (step == 9) {
          this.getFlssInfo(this.company_id); //法律诉讼
        }
      },
      tojdzs() {
        // this.$router.push({
        //   name: 'jdzsHomepage'
        // });
        this.$router.push({
          name: 'jdzsHomepage'
        })
      },
    }
  };
</script>

