<template>
	<div class="flextable_box table" ref="tablebox" @click="hideinfo">
		<el-table :data="tableData" border style="width: 100%" :max-height="maxheight">
			<el-table-column width="220" v-for="(item,index) in tabelHeader" :index="index" :key="index" :render-header="renderHeader">
				<template slot-scope="scope">
					<div v-if="!scope.row.edited&&!scope.row.newcolumn">
						<div v-if="item[3]==1">
							<span v-if="scope.row[item[0]]==0">否</span>
							<span v-if="scope.row[item[0]]==1">是</span>
						</div>
						<div v-else>{{scope.row[item[0]]}}</div>
					</div>
					<div v-else class="module_ckmx_td_input">
						<div v-if="item[3]==0">{{scope.row[item[0]]}}</div>
						<el-select v-if="item[3]==1" v-model="scope.row[item[0]]" placeholder="请选择">
							<el-option v-for="select in selectList" :key="select.id" :label="select.name" :value="select.id">
							</el-option>
						</el-select>
						<el-select v-if="item[3]==3" v-model="scope.row[item[0]]" placeholder="请选择">
							<el-option v-for="select in exitList" :key="select.id" :label="select.name" :value="select.name">
							</el-option>
						</el-select>
						<el-date-picker v-if="item[3]==5" :editable='false' v-model="scope.row[item[0]]" type="date" value-format="yyyy-MM-dd"  placeholder="选择日期">
    					</el-date-picker>
						<el-select v-if="item[3]==4" v-model="scope.row.fundId" placeholder="请选择">
							<el-option v-for="select in fundList" :key="select.id" :label="select.name" :value="select.id">
							</el-option>
						</el-select>
						
						
						<el-input v-if="item[3]==2" v-model="scope.row[item[0]]" placeholder="请输入内容"></el-input>
					</div>
				</template>
			</el-table-column>
			<el-table-column fixed="right" label="操作" width="100" v-if="fundId!=-1">
				<template slot-scope="scope">
					<div v-if="scope.row.newcolumn" class="">
						<el-button class="el_button" @click="handleAddClick(scope.row)" type="text" size="small">提交</el-button>
						<el-button class="el_button" @click="handleClearClick(scope.row)" type="text" size="small">清空</el-button>
					</div>
					<div v-else>
						<div v-if="scope.row.edited">
							<el-button class="el_button" @click="handleEditsaveClick(scope.row)" type="text" size="small">保存</el-button>
							<el-button class="el_button" @click="handleCancelEditClick(scope.row)" type="text" size="small">取消</el-button>
						</div>
						<div v-else>
							<el-button class="el_button" @click="handleEditClick(scope.row)" type="text" size="small">编辑</el-button>
							<!-- <el-button class="el_button" @click="handleDelClick(scope.row)" type="text" size="small">删除</el-button> -->
						</div>
					</div>
				</template>
			</el-table-column>
		</el-table>
	</div>
</template>
<script>
	import { castProjExplain, operateCastProj, allProjectOrFund } from '../../config/api'
	import { getMjjProList,editMjjPro, getPetFundProList, editPetFundPro,getMjjRunList,getPetFundRunList } from '../../config/apiSj'
	//import fundLead from '../../pages/fundrunComponent/fundLead.vue'
	import {
		dialogConfirm,
		dialogConfirmHasbtn,
		compareJson
	} from '../../config/mUtils'
	export default {
		data() {
			return {
				maxheight: 500,
				tabelHeader: [],
				tableData: [],
				notNulllist: ['is_oversea', 'creditCode', 'regLocation', 'exit_case'],
				//fundId: 4,
				selectList: [{
					id: 1,
					name: '是'
				}, {
					id: 0,
					name: '否'
				}],
				exitList: [{
					id: 0,
					name: '完全退出'
				}, {
					id: 1,
					name: '部分退出'
				}, {
					id: 2,
					name: '未退出'
				}],
				fundList: [],
				newcolumn: '',
				oldcolumn: '',
			}
		},
		components: {
			//fundLead
		},
		created() {

			this.getFundlist();
			this.getHeader();
		},
		computed: {},
		props: ['fundId', 'height'],
		mounted() {
			/*if(this.$refs.tablebox.offsetHeight<200&&this.$refs.tablebox.offsetHeight>400){
			  this.maxheight=400;
			}else{
			  this.maxheight=this.$refs.tablebox.offsetHeight;
			}*/
		},
		methods: {
			//获取基金列表
			getFundlist() {
				allProjectOrFund({
					type: 2,
					flag: 0
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.fundList = res.data.payload.data;
					}
				})
			},
			//表格数据
			getHeader() {
				castProjExplain().then(res => {
					if(res.data.msgNo == 1) {
						let th = [];
						let nunname = ['fundId', 'fundName'];
						if(this.fundId == -1) {
							res.data.payload.forEach(item => {
								th.push(item);
							})
						} else {
							res.data.payload.forEach(item => {
								if(nunname.indexOf(item[0]) == -1) {
									th.push(item);
								}
							})
						}
						let newcolumn = {};
						th.forEach(item1 => {
							let name = item1[0];
							if(name == 'seq'||name == 'name') {
								item1.push(0)
							} else if(name.indexOf('is_') != -1) {
								item1.push(1)
							} else if(name == 'exit_case') {
								item1.push(3)
							} else if(name == 'fundName') {
								item1.push(4)
							}else if(name == 'estiblishTime') {
								item1.push(5)
							} else {
								item1.push(2)
							}
							newcolumn[name] = '';
						})
						newcolumn.fundId = '';
						this.tabelHeader = th;
						this.getTabledata(1, newcolumn);
					}
				});
			},
			getTabledata(flag, newcolumn) { //flag=>1 查询，2.添加，3.修改，4.删除，5=>总览
				newcolumn = JSON.parse(JSON.stringify(newcolumn));
				// let param = {
				// 	fundId: flag
				// };
				// if(this.fundId == -1 && flag == 1) {
				// 	param.flag = 5
				// } else {
				// 	param.fundId = this.fundId
				// }
				if(this.$route.query.type==1){
					// console.log(this.fundId);
					if(this.fundId==-1){
						getMjjRunList({
							page:1,
							num:1000,
						}).then(res => {
							if(res.data.msgNo == 1) {
								let td = res.data.payload.data;
								td.forEach((item, index) => {
									item.edited = false;
									item.newcolumn = false
								});
								newcolumn.seq = td.length + 1;
								newcolumn.newcolumn = true;
								this.newcolumn = JSON.parse(JSON.stringify(newcolumn));
								this.tableData = td;
							}
						})
					}else{
						getMjjProList({
							fof_id:this.fundId,
						}).then(res => {
							if(res.data.msgNo == 1) {
								let td = res.data.payload.data;
								td.forEach((item, index) => {
									item.edited = false;
									item.newcolumn = false
								});
								newcolumn.seq = td.length + 1;
								newcolumn.newcolumn = true;
								this.newcolumn = JSON.parse(JSON.stringify(newcolumn));
								this.tableData = td;
							}
						})
					}
					
				}else if(this.$route.query.type==2){
					if(this.fundId==-1){
						getPetFundRunList({
							page:1,
							num:1000,
						}).then(res => {
							if(res.data.msgNo == 1) {
								let td = res.data.payload.data;
								td.forEach((item, index) => {
									item.edited = false;
									item.newcolumn = false
								});
								newcolumn.seq = td.length + 1;
								newcolumn.newcolumn = true;
								this.newcolumn = JSON.parse(JSON.stringify(newcolumn));
								this.tableData = td;
							}
						})
					}else{
						getPetFundProList({
							petfund_id:this.fundId,
						}).then(res => {
							if(res.data.msgNo == 1) {
								let td = res.data.payload.data;
								td.forEach((item, index) => {
									item.edited = false;
									item.newcolumn = false
								});
								newcolumn.seq = td.length + 1;
								newcolumn.newcolumn = true;
								this.newcolumn = JSON.parse(JSON.stringify(newcolumn));
								this.tableData = td;
							}
						})
					}

					
				}
			},
			renderHeader(createElement, {
				column,
				$index
			}) {
				let that = this;
				let label = this.tabelHeader[column.index];
				let require = '';
				if(that.notNulllist.indexOf(label[0]) != -1) {
					require = '*'
				} else {
					require = ''
				}
				return createElement(
					'div', {
						'class': 'header-center',
						attrs: {
							'data-index': column.index
						},
						on: {}
					}, [
						createElement('div', {
							'class': 'header-center-title',
						}, [
							createElement('el-tooltip', {
								props: {
									effect: "light",
									placement: 'top',
									//content: label[2]
								},
							}, [
								createElement('div', {
									class: 'header-center-info',
									slot: 'content'
								}, label[2]),
								createElement('div', {
									'class': 'header-center-text',
								}, [
									createElement('span', {
										class: 'fcolor_red fsize_20',
									}, require),
									createElement('span', {
										class: 'header-center-text',
									}, label[1]),
									createElement('span', {
										'class': 'header-center-icon el-icon-question',
									})
								]),

							]),
						])
					],
				);
			},
			//保存修改
			handleSaveClick(row) {
				row.edited = !row.edited;
				this.oldcolumn = row;
			},
			//清空新增
			handleClearClick(row) {
				for(let i in this.newcolumn) {
					row[i] = this.newcolumn[i];
				}
			},
			//新增
			handleAddClick(row) {
				let param = JSON.parse(JSON.stringify(row));
				param.flag = 2;
				if(this.fundId != -1) {
					param.fundId = this.fundId;
				} else {
					if(param.fundId == '') {
						dialogConfirm('info', '基金名称不能为空');
						return;
					}
				}
				var test = this.checkNotnull(param);
				if(test) {
					operateCastProj(param).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm('success', '提交成功');
							this.getTabledata(1, this.newcolumn);
						} else {
							dialogConfirm('info', res.data.message);
						}
					})
				} else {
					dialogConfirm('info', '您还有必填项未填')
				}
			},
			//检测非空项
			checkNotnull(param) {
				for(let i in param) {
					if(this.notNulllist.indexOf(i) != -1) {
						if(param[i] != 0 && param[i] == '') {
							return false
						}
					}
				}
				return true;
			},
			//编辑
			handleEditClick(row) {
				row.edited = !row.edited;
				this.oldcolumn = JSON.parse(JSON.stringify(row));
			},
			//取消编辑
			handleCancelEditClick(row) {
				let flag = compareJson(this.oldcolumn, row);
				if(flag == false) {
					dialogConfirmHasbtn('确认取消编辑吗？').then(res => {
						for(let i in row) {
							row[i] = this.oldcolumn[i]
						}
						row.edited = false;
					}).catch(() => {})
				} else {
					row.edited = false;
				}
			},
			//编辑提交
			handleEditsaveClick(row) {
				let flag = compareJson(this.oldcolumn, row);
				let param = JSON.parse(JSON.stringify(row));
				if(flag == false) {
					// param.flag = 3;
					// if(this.fundId != -1) {
					// 	param.fundId = this.fundId;
					// }
					// console.log(param);
					// return false;
					var test = this.checkNotnull(param);
					if(test) {
						if(this.$route.query.type==1){
							editMjjPro(param).then(res => {
								if(res.data.msgNo == 1) {
									dialogConfirm('success', '修改成功');
									this.getTabledata(1, this.newcolumn);
								} else {
									dialogConfirm('info', res.data.message);
								}
							})
						}else if(this.$route.query.type==2){
							editPetFundPro(param).then(res => {
								if(res.data.msgNo == 1) {
									dialogConfirm('success', '修改成功');
									this.getTabledata(1, this.newcolumn);
								} else {
									dialogConfirm('info', res.data.message);
								}
							})
						}
						
					} else {
						dialogConfirm('info', '您还有必填项未填～')
					}
				} else {
					row.edited = false;
				}
			},
			//删除
			handleDelClick(row) {
				let param = {
					flag: 4,
					id: row.id
				}
				if(this.fundId == -1) {
					param.fundId = row.fundId;
				} else {
					param.fundId = this.fundId;
				}
				operateCastProj(param).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '删除成功～');
						this.getTabledata(1, this.newcolumn);
					} else {
						dialogConfirm('info', res.data.message)
					}
				})
			},
			hideinfo() {
				let dom = document.getElementsByClassName('table_header_infobox');
				if(dom.length > 0) {
					for(let i = 0; i < dom.length; i++) {
						document.body.removeChild(dom[i]);
					}
				}
			},
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/folder.scss';
	@import '../../style/fund.scss';
	.flextable_box {
		margin-bottom: 20px;
		height: 100%;
		overflow: auto;
	}
	
	.twoGrade_box {
		margin-bottom: 20px;
	}
	
	.header-center {
		line-height: 1.2em;
		white-space: normal;
	}
	
	.table_header_infobox {
		position: fixed; //right: 0px;
		z-index: 103;
		padding: 12px;
		border-radius: 5px; //width: 400px;
		box-sizing: border-box;
		background: #35383A;
		margin-top: -30px;
		max-width: 600px;
		transform: translate(-50%, -100%); //margin-left: -12px;
		.info {
			color: #fff;
			font-size: 13px;
			max-height: 180px;
			overflow-y: auto; //float: left;
		}
		.span {
			position: absolute;
			bottom: -6px; //
			left: 50%;
			margin-left: -6px;
			display: block;
			border-width: 6px 6px 0px;
			border-style: solid;
			border-color: #35383A transparent transparent;
			/*透明 透明  灰*/
		}
	}
	
	.header-center-icon {
		color: #A0A4AA;
	}
	
	.header-center-text:hover {
		cursor: pointer;
		color: #3C98E8;
		.header-center-icon {
			color: #3C98E8;
		}
	}
	
	.header-center-info {
		color: #fff;
		max-width: 400px;
		font-size: 13px;
		line-height: 1.2em;
	}
	
	.header-center-icon:hover {
		cursor: pointer;
		color: #3C98E8;
	}
	
	.el_button {
		span {
			color: #3C98E8
		}
	}
</style>


