<template>
  <div class=" investHome">
    <!-- <div class="content-title clear">
      <div class="left">项目管理</div>
    </div> -->
    <!-- <div class="line"></div> -->
    <!-- <div class="account-group"> -->
      <div class="content-body">
        <div class="content-body-class" v-for="(item,index) in data" :key="index">
          <div class="content-body-class-title">{{item.name}}</div>
          <div class="content-body-class-items clear">
            <div class="content-body-class-item" v-for="(val,index) in item.child" :key="index" @click="goDetail(val.id,item)">
              <img :src="'../static/images/investProjectManageHome_'+val.path+'.png'" alt="">
              <div class="content-body-class-item-name">{{val.name}}({{val.integrity}})</div>
            </div>
          </div>
        </div>
      </div>
    <!-- </div> -->
  </div>
</template>
<script>
import {
  projOutline
} from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      data: [],
      projId: 185,
      companyName: null,
    }
  },
  mounted() {
    let params = this.$route.query;
    this.projId = params.id;
    this.getHomedataList();
  },
  methods: {
    getHomedataList() {
      projOutline({
        projId: 185,
      }).then(res => {
        if (res.data.msgNo == 1) {
          let data = res.data.payload.data;
          this.data = data;
          this.companyName = res.data.payload.name
        }
      })
    },
    goDetail(id, item) {
      this.$router.push({
        path: '/home/investProjectManageDetail',
        query: {
          id: id,
          projId: this.projId,
        }
      })
    }
  }
};

</script>
<style lang="scss" scoped>
.investHome {
  width: 100%;
  min-height: 100%;
  background-color: white;
  // padding: 36px 40px;
}

.content-title {
  max-width: 1160px;
}

.content-title>div.left {
  font-size: 16px;
  line-height: 1em;
  color: #35383A;
  margin-bottom: 18px;
  font-weight: bold;
}

.account-group {
  width: 100%;
  /* max-width: 1160px;
        border: 1px solid #E4E4E4;
        padding: 30px 18px; */
}

.historyapply-group-top {
  margin-bottom: 41px;
  height: 30px;
  line-height: 30px;
}

.historyapply-group-top-dropdown span {
  font-size: 18px;
  color: #3C98E8;
  position: relative;
  top: 1px;
  padding-right: 15px;
}

.content-body {
  display: flex;
  justify-content: space-between;
}

.content-body-class {
  background: #F9FBFE;
  padding: 16px;
  width: 25%;
  &:first-child {
    width: 48%;
    .content-body-class-item {
      width: 48%;
    }
  }
}

.content-body-class-title {
  margin-bottom: 30px;
  color: #35383A;
  font-size: 14px;
  margin-bottom: 10px;
}

.content-body-class-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.content-body-class-item {
  width: 100%;
  min-width: 200px;
  height: 50px;
  background: #fff;
  display: flex;
  align-items: center;
  padding: 16px;
  cursor: pointer; //margin-right: 20px;
  margin-bottom: 16px;
  transition: all 0.3s;
  border-radius: 4px;
  &:hover {
    // background: #E7E8EA;
    // transform: scale(1.05)
    box-shadow: 0px 0px 40px 0px rgba(215, 225, 246, 1);
  }
}

.content-body-class-item-name {
  font-size: 16px;
  color: #1A1918;
}

.content-body-class-item img {
  width: 24px;
  height: 24px;
  border-radius: 5px;
  margin-right: 18px;
}

.content-body-class-item img:hover {
  opacity: 0.8;
  cursor: pointer;
}

.line {
  width: 100%;
  border-top: 1px solid #F4F4F4;
  /* margin-top: 42px; */
  margin-bottom: 17px;
}

</style>



