			border-bottom: 1px solid white;
		}
	} //表格样式
	.table-normal {
		margin-bottom: 30px;
	}
	
	.bgjl-table-normal {
		margin-top: 30px;
	}
	
	.bgjl-table-normal>tr>th {
		padding: 5px 18px; // text-align: left;
	}
	
	.qynb-table-normal>tr>th {
		padding: 5px 18px;
		text-align: left;
	}
	
	.table-normal>.noColspanTr>th {
		text-align: left;
		color: #35383a;
		font-weight: 500;
	}
	
	.table-normal>.noColspanTr>td {
		text-align: left;
	}
	
	.bold {
		font-weight: 500;
		color: #35383a;
	}
	
	.qynbValue {
		color: #35383a;
	}
	
	.qynb-title {
		padding: 25px 0 11px 24px;
		font-size: 18px;
		color: #35383a;
		font-weight: bold;
	} //股权结构
	.custom-tree-node {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 14px;
		padding-right: 8px;
		.node-title-text {
			color: #a0a4aa;
			font-size: 14px;
			position: relative;
			padding-right: 32px;
			&::after {
				width: 1px;
				height: 18px;
				position: absolute;
				right: 14px;
				top: -1px;
				transform: rotate(18deg);
				content: "";
				display: inline-block;
				background: #e3e3e3;
			}
			&:last-child::after {
				background: none;
			}
			em {
				color: #35383a;
				font-size: 14px;
				margin-left: 5px;
			}
		}
		.node-title-input {
			line-height: 22px;
			color: #35383a;
			font-size: 14px;
			border: 1px solid #e4e4e4;
			padding: 0px 5px;
			width: 80px;
			margin-left: 3px;
			&:focus {
				border-color: #3c98e8;
				box-shadow: 0px 0px 2px 0px rgba(60, 152, 232, 1);
			}
		} // .node-title-icon {
		//   display: none;
		//   cursor: pointer;
		//   width: 22px;
		//   height: 22px;
		//   background: url('../../images/businessinfo_icon.png') no-repeat;
		//   margin-left: 15px;
		// }
		.node-title-icon:hover {
			background-position-x: -22px;
		}
		.icon-delete {
			background-position-y: -44px;
		}
		.icon-child {
			background-position-y: -22px;
