		.node-title-edit {
			color: #fff;
		}
		.node-title-movein {
			.node-title-input {
				border-color: #3c98e8;
				box-shadow: 0px 0px 2px 0px rgba(60, 152, 232, 1);
			}
		}
		.node-title {
			&::before {
				content: "";
				display: inline-block;
				float: left;
				width: 2px;
				height: 14px;
				margin-top: 5px;
				margin-right: 8px; //background: #F55D54;
			}
			font-size: 14px;
			color: #a0a4aa;
			line-height: 24px;
		}
		.node-title-1 {
			&::before {
				background: #f55d54;
			}
		}
		.node-title-2 {
			&::before {
				background: #7a79bc;
			}
		}
		.node-title-3 {
			&::before {
				background: #f15266;
			}
		}
		.node-title-4 {
			&::before {
				background: #4998c0;
			}
		}
		.node-title-5 {
			&::before {
				background: #ed7f76;
			}
		}
		.node-title-6 {
			&::before {
				background: #a3c6e4;
			}
		}
		.node-title-7 {
			&::before {
				background: #f55d54;
			}
		}
		.node-title-8 {
			&::before {
				background: #c5e8d1;
			}
		}
		.node-title-9 {
			&::before {
				background: #ed7f76;
			}
		}
		.node-title-10 {
			&::before {
				background: #dfe0aa;
			}
		}
	} // .custom-tree-node:hover {
	//   .node-title-icon {
	//     display: inline-block;
