<template>
<!--  股东信息-->
  <div class="viewapply-content-normal">
     <div class="table-nodata" v-if="companyInfo.length==0">
      <img src="../images/table_nodata.png"> 没有对应数据
    </div>
    <div class="viewapply-content-item" :style="itemstyle"  v-for="(item,index) of companyInfo" :key="index" v-else>
      <img class="companyimg" v-bind:src='item.logo' alt="">
      <div class="right-box">
        <div class="name">{{item.name}}</div>
        <div class="time">认缴时间：{{item.time}}</div>
        <div class="share">
          <div class="count count-border">
            <div class="num">{{item.amount}}</div>
            <div class="text">认缴出资额</div>
          </div>
          <div class="count">
            <div class="num num-red">{{isNaN(item.percent)?'未知':item.percent}}%</div>
            <div class="text">持股比例</div>
          </div>
          <el-progress class="scalecicle" type="circle" :percentage="isNaN(item.percent)?0:item.percent" color="#FF7676" :show-text="false" :width="50"></el-progress>
        </div>
      </div>
    </div>
   
  </div>
</template>
<script>
export default {
  data() {
    return {
    }
  },
  mounted() {

  },
  created() {
  },
  computed: {
    itemstyle:function(){
      return {
        width:this.itemWidth,
        height:this.itemHeight
      }
    },
  },
  props: ['companyInfo','itemWidth','itemHeight'],
  methods: {
  }
}

</script>
<style lang="scss" scoped>
@import './../style/mixin';
.viewapply-content-normal {
  display: flex;
  flex-wrap:wrap;
  .viewapply-content-item {
    width: 100%;
    display: flex;
    padding: 15px;
    background: #fff;
    box-shadow:0px 0px 10px 0px rgba(215,225,246,1);
    margin-right: 16px;
    margin-bottom: 20px;
    .right-box{
      position: relative;
    }
    .companyimg{
      width: 40px;
      height: 40px;
      border-radius: 50%;
      margin-right: 14px;
      flex-shrink: 0;
    }
    .name{
      font-size: 18px;
      line-height: 28px;
      margin-bottom: 4px;
      color: #35383A;
      font-weight: 500;
      width: 286px;
    }
    .time{
      font-size: 14px;
      color: #A0A4AA;
      line-height: 1em;
      margin-bottom: 20px;
    }
     .share{
      display: flex;
      position: absolute;
      bottom: 16px;
      width: 286px;
      justify-content:space-between;
      .count-border{
        border-right: 1px solid #F1F1F1;
      }
      .count{
        padding-right: 20px;
        .num{
          font-size: 22px;
          color: #3C98E8;
          margin-bottom: 10px;
          line-height: 1em;
        }
        .num-red{
          color: #FF7676;
        }
        .text{
          font-size: 12px;
          color: #A0A4AA;
          line-height: 1em;
        }
      }
     }
  }
}

</style>



