<template>
  <div class="introduce-header">
    <div class="introduce-logo">
      <div class="introduce-logo-left">
        <img src="../../images/login_logo.png" alt="" @click="tabclick('introducePage',1)">
        <img class="introduce-logo-line" src="../../images/login_header_line.png"> XPE 智能办公平台
      </div>
      <div class="introduce-logo-center">
        <div class="introduce_nav" :class="{current:index==0}" @click="tabclick('introducePage')">产品</div>
        <div class="introduce_nav" :class="{current:index==1}" @click="tabclick('vipprice')">价格</div>
        <div class="introduce_nav" :class="{current:index==2}" @click="tabclick('download')">下载</div>
      </div>
      <div class="introduce-logo-right">
        <!-- <span>已有账号？</span><span class="introduce-btn" @click="toLogin">登录</span> -->
        <div class="introduce-btn2" @click="toLogin">登&nbsp;&nbsp;录</div>
        <div class="introduce-btn2" @click="toRegiste">注&nbsp;&nbsp;册</div>
      </div>
    </div>
  </div>
</template>
<script>
import {
} from '../../config/api'
export default {
  inject:['reload'],
  
  data() {
    return {

    }
  },
  // watch: {
  //   $route(to, from) {
  //     console.log(1111);
  //     let name = to.name;
  //     let fromname = from.name;;
  //     console.log(fromname,name);
  //   }
  // },
  components: {},
  props:['index'],
  created() {

  },
  mounted() {},
  computed: {

  },
  methods: {
    toRegiste() {
      this.$router.push({
        path: '/register',
      })
    },
    toLogin() {
      this.$router.push({
        path: '/login',
      })
    },
    tabclick(name,type){
      // console.log(1111);
      // console.log(this.$route.path);
      if(this.$route.path=='/introducePage'&&name=='introducePage'){
        this.reload();
      }else{
        this.$router.push({
          name: name,
        })
      }
      
    },
  }
}

</script>
<style lang="scss" scoped>
.introduce-logo {
  width: 1200px;
  margin: 0 auto;
  font-size: 16px;
  display: flex;
  align-items: center;
  justify-content:space-between;
  color: #226FCE;
  position: relative;
  height: 84px;
  
  span {
    color: #A0A4AA;
  }
  .introduce-btn {
    color: #4390F0;
    cursor: pointer;
    margin-right: 15px;
  }
  .introduce-btn1 {
    width: 100px;
    height: 36px;
    line-height: 36px;
    text-align: center;
    cursor: pointer;
    border-radius: 4px;
    background-color: #4390F0;
    color: #fff; // margin-left: 45px;
  }
  .introduce-btn2{
    height: 100%;
    background: #3C98E8;
    color: #fff;
    font-size: 16px;
    width: 100px;
    display: flex;
    align-items:center;
    justify-content:center;
    cursor: pointer;
    &:first-child{
      border-left:1px solid #F1F1F1;
      color: #A0A4AA;
      background: none;
    }
  }
  img:first-child {
    width: 75px;
    height: 45px;
    cursor: pointer;
  }

  .introduce-logo-line {
    margin: 0px 15px;
  }
}
.introduce-logo-left{
  display: flex;
  align-items:center;
  color:#226FCE;
}
.introduce-logo-right{
  display: flex;
  align-items:center;
  height: 100%;
}
.introduce-logo-center{
  display: flex;
  height: 100%;
  align-items:center;
  .introduce_nav{
    height: 100%;
    display: flex;
    align-items:center;
    margin: 0px 35px;
    border-bottom: 3px solid #fff;
    box-sizing:border-box;
    cursor: pointer;
    &:hover{
      //border-bottom: 3px solid #3C98E8;
      color: #3C98E8;
    }
  }
  .introduce_nav.current{
    border-bottom: 3px solid #3C98E8;
  }
}
.introduce-header {
  width: 100%;
  min-width: 1200px;
  background: #fff;
  box-shadow: 0px 2px 10px 0px #ffffff;
  position: fixed;
  top: 0px;
  z-index: 2;
  border-bottom: 1px solid #F1F1F1;
}

</style>



